.intro {
  min-height: 700px;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  background: url('../img/bg_up_slide.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  &__content {
    display: flex;
    align-items: center;
    height: 100%;
    @include r(1200) {
      padding-left: 60px; }
    @include r(1024) {
      padding-right: 40px; }
    @include r(767) {
      padding: 0; }
    span {
      font-family: 'SF Pro Text';
      font-size: 20px;
      font-weight: normal;
      font-style: normal;
      color: $greyish-two;
      @include r(767) {
        font-size: 15px; } }
    h2 {
      font-weight: bold;
      line-height: 1;
      letter-spacing: 3px;
      color: $white;
      font-size: 100px;
      padding-top: 20px;
      margin-bottom: 85px;
      @include r(767) {
        font-size: 50px; } }
    p {
      font-size: 30px;
      font-weight: normal;
      font-style: italic;
      line-height: 1.33;
      letter-spacing: 1px;
      color: $pale-olive;
      max-width: 730px;
      margin-bottom: 70px;
      @include r(767) {
        font-size: 20px; } } } }
