.team {
  transform: skewX(-6deg) translate(-50%);
  background: $apple;
  border-top-left-radius: 80px;
  border-bottom-right-radius: 80px;
  height: 1030px;
  width: 105%;
  position: relative;
  z-index: 2;
  left: 50%;
  @include r(1024) {
    transform: none;
    left: 0;
    height: auto;
    width: 100%;
    padding: 50px 0; }
  .heading {
    p {
      color: $white; } }
  &--block {
    position: relative;
    overflow: hidden;
    &.team--services-card {
      &:before {
        content: '';
        position: absolute;
        top: 0;
        height: 50%;
        width: 100%;
        background: $white;
        z-index: 1; }
      .team {
        transform: skewX(-12deg) translate(-50%);
        @include r(1024) {
          transform: none; }
        &__content {
          transform: skewX(12deg);
          @include r(1024) {
            transform: none; } } } } }
  &__content {
    transform: skewX(6deg);
    display: flex;
    align-items: center;
    height: 100%;
    @include r(1024) {
      transform: none; } }
  .swiper {
    position: relative;
    &-container {
      overflow: visible;
      @include r(1024) {
        overflow: hidden; }
      .swiper-slide.next, .swiper-slide.prev {
        opacity: 0.4;
        @include r(1024) {
          opacity: 1; }
        h3, p, a.btn {
          opacity: 0;
          @include r(1024) {
            opacity: 1; } }
        .team__block__img {
          width: 150px;
          height: 150px;
          margin-right: -150px;
          margin-top: 30px;
          @include r(1024) {
            width: 200px;
            height: 200px;
            margin: 0;
            margin-bottom: 30px; } } }
      .swiper-slide.next {
        .team__block__img {
          margin-right: 150px; } } }
    &-slide {
      display: flex;
      flex-direction: column;
      align-items: center; } } }
