.services-intro {
  min-height: 700px;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  background: url('../img/bg-services.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  p {
    font-size: 30px;
    font-weight: normal;
    line-height: 1.33;
    letter-spacing: 1px;
    color: $pale-olive;
    max-width: 730px;
    margin-bottom: 130px; }
  &__nav {
    display: flex;
    margin-bottom: 100px;
    @include r(500) {
      flex-direction: column;
      align-items: center; }
    a {
      font-size: 30px;
      font-weight: normal;
      letter-spacing: 1px;
      color: $apple;
      display: inline-block;
      padding-right: 30px;
      @include r(500) {
        margin-bottom: 10px; } } } }
.sport, .health, .beauty {
  position: relative;
  overflow: hidden;
  margin-top: 100px;
  margin-bottom: 120px;
  .heading {
    margin-bottom: 120px; } }
