.footer {
  background: $pine-green;
  padding: 70px 0;
  @include r(767) {
    padding-bottom: 20px; }
  &__map {
    background: $pine-green;
    padding-bottom: 35px;
    margin-bottom: 70px;
    border-bottom: 1px solid $apple;
    h4 {
      a {
        font-size: 24px;
        font-weight: 600;
        line-height: 1.42;
        letter-spacing: 0.8px;
        color: $apple;
        margin-bottom: 15px;
        padding-top: 10px;
        display: block; } }
    ul {
      margin-bottom: 30px;
      li {
        a {
          font-family: 'SF Pro Text';
          font-size: 18px;
          font-weight: normal;
          font-style: normal;
          line-height: 1.44;
          letter-spacing: 0.6px;
          color: $pale-olive; } } }
    &__map {
      margin-top: 25px;
      display: block; } }
  &__logo {
    max-width: 160px;
    @include r(767) {
      position: relative;
      left: 50%;
      transform: translate(-50%);
      margin-bottom: 20px; }
    img {
      width: 100%; } }
  p {
    font-family: 'SF Pro Text';
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    color: $warm-grey; }
  &__social {
    display: flex;
    justify-content: center;
    a {
      margin-right: 20px;
      color: $pale-olive;
      i {
        font-size: 23px; }
      &:last-child {
        margin-right: 0; } } }
  a.policy {
    font-family: 'SF Pro Text';
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    color: $warm-grey;
    text-align: center;
    display: block;
    @include r(767) {
      padding: 20px 0; } }
  &__done {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    @include r(767) {
      align-items: center;
      margin-top: 30px; }
    img {
      height: 35px;
      display: block; }
    p {} } }


