.saline-room {
  padding: 70px 0;
  padding-bottom: 100px;
  background: url('../img/shutterstock-657941563.png') no-repeat;
  background-size: cover;
  @include r(767) {
    padding-bottom: 30px; }
  &__title {
    p {
      font-size: 30px;
      font-weight: normal;
      line-height: 1.33;
      letter-spacing: 1px;
      color: $white;
      margin-bottom: 55px;
      max-width: 735px;
      @include r(767) {
        font-size: 20px; } } }
  &__block {
    @include r(767) {
      @include center;
      flex-direction: column; }
    &__img {
      min-height: 150px;
      @include center;
      justify-content: flex-start; }
    p {
      font-size: 24px;
      font-weight: 600;
      line-height: 1.42;
      letter-spacing: 0.8px;
      color: $white;
      max-width: 215px;
      @include r(767) {
        text-align: center; } } } }
.feel {
  position: relative;
  padding-top: 100px;
  padding-bottom: 120px;
  @include r(767) {
    padding-bottom: 0px; }
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 700px;
    height: 90px;
    background: $white;
    clip-path: polygon(0 0%, 0% 0, 100% 100%, 0 100%);
    @include r(767) {
      display: none; } }
  p {
    font-family: 'SF Pro Display';
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.42;
    letter-spacing: 1px;
    color: $pale-olive;
    @include r(767) {
      margin-bottom: 30px; } }
  &__video {
    border-radius: 7px;
    border-bottom-right-radius: 90px;
    border-top-left-radius: 90px;
    transform: skewX(-11deg);
    height: 335px;
    width: 93%;
    overflow: hidden;
    @include r(767) {
      position: relative;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) skewX(-11deg);
      width: 88%; }
    a {
      transform: skewX(11deg);
      height: 100%;
      width: 100%;
      display: block;
      position: relative;
      img {
        width: 110%;
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); }
      i {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 100px;
        color: $apple;
        transition: all 0.3s; }
      &:hover {
        i {
          color: $white; } } } } }
.features {
  background: $white;
  position: relative;
  padding: 120px 0;
  padding-bottom: 10px;
  overflow: hidden;
  @include r(767) {
    padding-top: 50px; }
  &__block {
    margin-bottom: 140px;
    margin-top: 50px;
    min-height: 400px;
    posr {}
    @include r(767) {
      margin-top: 0; }
    h3 {
      font-size: 40px;
      font-weight: bold;
      letter-spacing: 0.5px;
      color: $charcoal;
      margin-bottom: 65px; }
    p {
      font-family: 'SF Pro Display';
      font-size: 24px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.42;
      letter-spacing: 1px;
      color: #333333;
      max-width: 440px;
      @include r(767) {
        font-size: 20px; } }
    &__text {
      max-width: 445px;
      padding-left: 10px;
      @include r(767) {
        max-width: 100%;
        margin-top: 15px; }
      &-secondary {
        position: relative;
        left: 100%;
        transform: translate(-100%); } }
    &__img {
      width: 49%;
      position: absolute;
      overflow: hidden;
      height: 400px;
      border-radius: 5px;
      @include r(767) {
        position: relative;
        width: 100%;
        transform: none;
        left: 0;
        right: 0; }
      &-primary {
        left: -50px;
        border-bottom-right-radius: 90px;
        transform: skewX(-12deg);
        .features__block__img__content {
          @include center;
          width: 100%;
          height: 100%;
          transform: skewX(12deg);
          @include r(767) {
            transform: none; } } }
      &-secondary {
        right: -50px;
        border-bottom-left-radius: 90px;
        transform: skewX(12deg);
        .features__block__img__content {
          @include center;
          width: 100%;
          height: 100%;
          transform: skewX(-12deg);
          margin-left: -50px;
          @include r(767) {
            transform: none;
            margin: 0; } } } } } }
