.header {
  background: url('../img/bg_top.png');
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  height: 100px;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  z-index: 11;
  transition: all 0.3s;
  .container {
    max-width: 1100px; }
  &.bg {
    background: url(''); }
  a, p {
    font-size: 18px;
    font-weight: normal;
    font-style: italic;
    color: $apple;
    line-height: 30px; }
  &__block {
    display: flex;
    align-items: center; }
  &__services {
    display: inline-flex;
    @include r(1024) {
      display: none; }
    p {
      cursor: pointer;
      margin-right: 40px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        bottom: 4px;
        width: 100%;
        height: 2px;
        background: none;
        transition: all 0.3s; }
      &:hover {
        &:before {
          background: $apple; } }
      &.active {
        i {
          transform: translate(-50%) rotate(180deg); } }
      i {
        font-size: 8px;
        position: absolute;
        top: 41%;
        transform: translate(-50%);
        right: -23px;
        transition: all 0.3s; } }
    &__option {
      display: none;
      position: absolute;
      width: 100%;
      top: 100px;
      padding: 25px 10px 50px 10px;
      left: 0;
      background: rgba(#1c1c1c, 0.9);
      background-position: 50% 0;
      border-radius: 4px;
      &__block {
        max-width: 255px;
        margin-right: 10px; }
      .container {
        display: flex;
        justify-content: space-between; }
      h4 {
        font-size: 24px;
        font-weight: 600;
        font-style: italic;
        line-height: 1.42;
        letter-spacing: 0.8px;
        color: $apple; }
      li {
        display: block;
        padding: 8px 0; }
      a {
        font-family: 'SF Pro Display';
        font-size: 18px;
        font-weight: normal;
        font-style: normal;
        letter-spacing: 0.6px;
        color: $pale-olive;
        display: block;
        line-height: 1.1; }
      li:hover {
        background: rgba($apple, 0.4); } } }
  &__user {
    ul {
      display: flex;
      justify-content: space-between;
      @include r(1024) {
        display: none; }
      li {
        i {
          font-size: 14px;
          padding-left: 10px; } } } }
  &__logo {
    height: 50px;
    img {
      height: 50px;
      @include r(767) {
        height: 40px; } } }
  .menu-open {
    width: 30px;
    height: 30px;
    margin-right: 50px;
    display: inline-flex;
    @include r(767) {
      left: 20px;
      position: absolute; }
    &.active {
      span {
        background: transparent;
        &:before {
          width: 30px;
          transform: rotate(315deg);
          top: 0; }
        &:after {
          width: 30px;
          transform: rotate(-315deg);
          top: 0; } } }
    span {
      width: 15px;
      height: 2px;
      background: $apple;
      display: block;
      position: relative;
      top: 15px;
      &:before {
        content: '';
        position: absolute;
        width: 23px;
        height: 2px;
        top: -10px;
        background: $apple;
        left: 0;
        transition: all 0.5s; }
      &:after {
        content: '';
        position: absolute;
        top: 10px;
        width: 30px;
        height: 2px;
        background: $apple;
        left: 0;
        transition: all 0.5s; } } } }
